body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  color: #3C3C3B;
  background-color: #ffffff;
}
* {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  display: block;
  text-decoration: none;
  color: inherit;
}
img {
  vertical-align: bottom;
}
button {
  border: none;
  background-color: transparent;
  outline: none;
}
.main {
  position: relative;
  min-height: 1036px;
}
@media (min-width: 1280px) {
  .main {
    padding-left: 22%;
  }
  .main::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 22%;
    height: 100%;
    box-shadow: 0 0 35px rgba(0, 0, 0, 0.1);
  }
  .main::after {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    z-index: -1;
    width: 33.6%;
    height: 965px;
    background-color: #95B03C;
  }
}
@media (min-width: 1920px) {
  .main {
    padding-left: 19.43%;
  }
  .main::before {
    width: 19.43%;
  }
  .main::after {
    width: 40.05%;
  }
}
.page-header__top-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 64px;
  padding-left: 10px;
  border-bottom: 2px solid #95B03C;
}
.page-header__top-wrapper--submenu-active {
  min-height: 61px;
  padding-left: 13px;
  border-bottom: 1px solid #F2F2F2;
}
.page-header__back-wrapper {
  position: relative;
  display: none;
}
.page-header__back-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.page-header__back-button {
  height: 33px;
  padding-left: 33px;
  font-weight: 500;
  font-size: 14px;
  line-height: 13px;
  letter-spacing: 0.02em;
  color: #AEAEAE;
}
@media (max-width: 1279px) {
  .page-header__top-wrapper--submenu-active > .page-header__back-wrapper {
    display: block;
  }
}
@media (min-width: 1280px) {
  .page-header__top-wrapper {
    min-height: auto;
    padding-left: 0;
    position: absolute;
    top: 27px;
    left: 2.3%;
    border-bottom: none;
  }
}
@media (min-width: 1920px) {
  .page-header__top-wrapper {
    left: 2.6%;
  }
}
.toggle {
  position: relative;
  width: 46px;
  height: 46px;
  margin-left: auto;
}
.toggle::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 3px;
  border-radius: 3px;
  background-color: #3C3C3B;
  box-shadow: 0 9px 0 0, 0 -9px 0 0;
}
.toggle--cross::before {
  display: none;
}
.toggle__icon {
  position: absolute;
  top: 15px;
  left: 10px;
  display: none;
}
.toggle__icon--active {
  display: block;
}
@media (min-width: 1280px) {
  .toggle {
    display: none;
  }
}
.logo {
  padding-bottom: 2px;
}
.logo--menu-active {
  display: none;
}
@media (min-width: 1280px) {
  .logo__image {
    width: 222px;
    height: auto;
  }
}
@media (min-width: 1920px) {
  .logo__image {
    width: 272px;
  }
}
.nav {
  display: flex;
  align-items: center;
  min-height: 55px;
  padding: 0 10px;
}
.nav__list-wrapper {
  top: 0;
  left: 0;
  z-index: 20;
  display: none;
  flex-direction: column;
  width: 100%;
  background-color: #ffffff;
}
.nav__list-wrapper--active {
  display: flex;
}
.nav__request-button {
  width: 138px;
  height: 43px;
  margin-left: auto;
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  background-color: #3C3C3B;
}
.nav__request-text-2 {
  display: none;
}
.nav__item,
.nav__sublist-item {
  border-bottom: 1px solid #F2F2F2;
}
.nav__link {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 60px;
  padding: 0 14px;
  font-size: 14px;
  line-height: 18px;
}
.nav__sublist {
  display: none;
  flex-direction: column;
}
@media (max-width: 1279px) {
  .nav {
    position: relative;
  }
  .nav__list-wrapper {
    position: absolute;
  }
  .nav__list--closed {
    display: none;
  }
  .nav__link {
    justify-content: space-between;
    padding-left: 7%;
    padding-right: 6.5%;
  }
  .nav__list-icon {
    transform: rotate(-90deg);
  }
  .nav__item--hidden {
    display: none;
  }
  .nav__item--menu-show > .nav__link > .nav__list-icon--active {
    display: none;
  }
  .nav__item--menu-show > .nav__sublist {
    display: flex;
    border-top: 1px solid #F2F2F2;
  }
  .nav__item--menu-show > .nav__link > .nav__text {
    width: auto;
    padding-bottom: 8px;
    font-weight: 500;
    font-size: 18px;
    line-height: 13px;
    border-bottom: 2px solid #95B03C;
  }
}
@media (min-width: 1280px) {
  .nav {
    align-items: flex-start;
    padding: 0;
  }
  .nav__list-wrapper {
    display: flex;
    width: 56.9%;
    background-color: transparent;
  }
  .nav__list {
    display: flex;
    justify-content: space-around;
    padding: 0 20px;
  }
  .nav__item {
    border: none;
  }
  .nav__link {
    min-height: 46px;
    padding: 0;
    line-height: 16px;
  }
  .nav__list-icon {
    display: none;
    padding-left: 4px;
    box-sizing: unset;
  }
  .nav__list-icon--active {
    display: block;
  }
  .nav__request-button {
    display: block;
    width: 14.7%;
    height: auto;
    min-height: 100px;
    padding: 0 2%;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-align: left;
  }
  .nav__request-text-1 {
    display: none;
  }
  .nav__request-text-2 {
    display: inline;
  }
}
@media (min-width: 1920px) {
  .nav__list-wrapper {
    width: 50.3%;
  }
  .nav__list {
    padding: 0 26px;
    padding-top: 5px;
  }
  .nav__link {
    min-height: 45px;
    letter-spacing: 0.06em;
  }
  .nav__list-icon {
    padding-left: 11px;
  }
  .nav__request-button {
    width: 14.5%;
    height: 103px;
    padding: 0 3.5%;
